import React from 'react';
import TextContainer from '../components/TextField/TextContainer';
import StandartLayout from './StandartLayout';
import Button from '../components/Button/Button';
import StepsContainer from '../components/StepsContainer/StepsContainer';
import './SecondLayout.css'


const style = {
    display: 'grid',
    gridTemplateRows: '3rem 3rem',
    gap: '1rem',
    placeItems: 'center',
    marginTop: '3rem',
    width: '50%'
}

const SecondLayout = () => {
    return (
        <StandartLayout>
            <TextContainer>
                {'    ?'.toUpperCase()}
            </TextContainer>
            <div style={style}>
                <Button className='medium' innerText={''} linkToPath='/testreactjs/third'/>
                <Button className='medium' innerText={''} linkToPath='/testreactjs/third'/>
            </div>
            <StepsContainer currentIndex={1}/>
        </StandartLayout>
    );
}
 
export default SecondLayout;