import React from 'react';
import TextContainer from '../components/TextField/TextContainer';
import StandartLayout from './StandartLayout';
import Button from '../components/Button/Button';
import './SecondLayout.css'
import BorderButton from '../components/BorderButton/BorderButton';
import StepsContainer from '../components/StepsContainer/StepsContainer';
import './FourthLayout.css';


const style = {
    display: 'grid',
    placeItems: 'center',
    marginTop: '3vh',
    marginBottom: '1vh'
}

const FourthLayout = () => {
    return (
		<StandartLayout>
			<TextContainer>
				{' ,     '.toUpperCase()}
			</TextContainer>
			<div className='btn-container' style={style}>
				<BorderButton className={'default'}>   </BorderButton>
				<BorderButton className={'default'}>    </BorderButton>
				<BorderButton className={'default'}>    </BorderButton>
				<BorderButton className={'default'}>     </BorderButton>
				<BorderButton className={'default'}>   </BorderButton>
			</div>
            <Button innerText={''} linkToPath="/testreactjs/fifth" />
			<StepsContainer currentIndex={3}/>
		</StandartLayout>
	);
}
 
export default FourthLayout;